import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import Container from 'react-bootstrap/Container';

import {login_user} from '../api/account_api'
import { navigate } from 'gatsby';

const container = {
	width:'60%',
	minWidth:'300px',
	maxWidth:'550px',
	height:'350px',
	padding:'30px',
	display:'flex',
	flexFlow:'column',
	justifyContent:'space-evenly',
	borderRadius:'12px',
	boxShadow:'2px 2px 5px 2px #4f4',
}


const formHeader = {
	margin:3
}
export default function LoginBox(){
	const [ number , setNumber ]= useState('');
	const [ password , setPassword ]= useState('');

	const [error_user , setError_user] = useState('')
	const [error_pass , setError_pass] = useState('')
	const [error_err , setError_err] = useState('')
	
	const submit_login = async ({number , password})=>{
		setError_user('')
	setError_pass('')
		setError_err('')
		let response = await login_user({number , password});
		console.log(response)
		if(response.success !== true){
			if(response.type === 'user'){
			return setError_user(response.msg)
		}
		if(response.type === 'password'){
			return setError_pass(response.msg)
		}
		if(response.type === 'error'){
			return setError_err('Couldn\'t connect to server')
		}
		
		}else{
		
			localStorage.setItem('CSRF_token',response.token);
			let { seller_id , name , email , number , currency} = response;
			let store_ = {seller_id , shop_name:name , email , number , currency }
			localStorage.setItem('store_',JSON.stringify(store_))
			navigate('/products');
		}
	}

	return (
	<div style={container} onKeyDown={e=>{if(e.keyCode===13){submit_login({number , password})}else{console.log(e.keyCode)}}}>
		<h3 style={formHeader}>
			Login
		</h3>
		<FloatingLabel
        controlId="floatingInput"
        label="Whatsapp Number"
        className="mb-3"
      >
        <Form.Control type="number" onChange={(e)=>setNumber(e.target.value)} placeholder="Area Code & Phone number" />
		<p class="form-error" style={{color:'red',fontSize:14,marginRight:12}}>{error_user}</p>
      </FloatingLabel>

      <FloatingLabel controlId="floatingPassword" label="Password">
        <Form.Control type="password" onChange={(e)=>setPassword(e.target.value)} placeholder="Password" />
		<p class="form-error" style={{color:'red',fontSize:14,marginRight:12}}>{error_pass}</p>
		<p class="form-error" style={{color:'red',fontSize:14,marginRight:12}}>{error_err}</p>
      </FloatingLabel>
	  
		<Button variant='primary' onClick={()=>submit_login({number , password})} >Login</Button>		
	</div>
	)
}